<template>
  <div v-if="step === 0">
    <div class="row">
      <div class="col mx-auto splash">
        <img src="../assets/box-min.png" class="img-fluid animate__animated animate__fadeIn" alt="..." id="splashimg">
        <p style="text-align: center; padding-left: 15px; padding-right: 15px; font-weight: 500; font-size: 20px;" class="animate__animated animate__fadeIn">Welcome to <b>Gifty™</b> - Giveaway platform.
      Gifty™ is the new best way to win valuable prizes, simply by using the app!
      Complete short surveys to receive gift cards, or qualify for "test and keep" programs for tech products!
      </p>
      <div class="row justify-content-center">
        <button class="animate__animated animate__fadeIn" @click="seen" id="button">See available products</button></div>
      </div>
    </div>
  </div>
  <div v-if="step === 1">
    <div class="splash">
      <div class="row animate__animated animate__fadeInUp" id="loading">
        <div class="col-md-4"></div>
        <div class="col-md-4">
          <p style="text-align: center; font-size: 20px; font-weight: 500">{{word}}</p>
          <div id="progress-bar">
            <progress class="progress progress-bar-striped progress-bar-animated" :value="width" max="100">
              <div class="progress">
                <span class="progress-bar" :style="{ width: width + '%', color: pbcolor }">{{width}}%</span>
              </div>
            </progress>
          </div>
          <div class="progress-info"></div>
        </div>
        <div class="col-md-4"></div>
      </div>
    </div>
  </div>
</template>

<script>

import router from "@/router";

export default {
  name: 'splash',
  data() {
    return {
      width: 0,
      step: 0,
      word: 'Preparing app...'
    }
  },
  methods: {
    seen() {
      this.step = 1
      this.progress()
    },
    progress() {
      let c = 0
      let b = setInterval(() => {
        switch (this.width) {
          case 20:
            this.word = 'Checking browser...'
            break
          case 50:
            this.word = 'Checking IP...'
            break
        }
        c++
        this.width++
        if (c === 100) {
          clearInterval(b)
          localStorage.setItem('seenSplash', 'true')
          router.push('/tech')
        }
      }, 55) //ms
    }
  },
}


</script>
<style>
#splashimg {
  padding-top: 100px;
}
#loading {
  margin-top: 90%;
  padding-top:40px;
  padding-bottom: 40px;
  border-radius: 10px;
  background: rgba( 255, 255, 255, 0.8 );
  box-shadow: 0 8px 32px 0 rgb(31 38 135 / 10%);
  backdrop-filter: blur( 10px );
  -webkit-backdrop-filter: blur( 4px );
  border: 1px solid rgba(255, 255, 255, 0.8);
  margin-left: 10px;
  margin-right: 14px;
}
#button {
  background-color: rgb(255, 138, 0);
  border: none;
  border-radius: 10px;
  width: 50%;
  color: white;
  padding: 15px 8px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
}
.splash {
  background-image: url(/src/assets/bg.jpg);
  background-size: cover;
  width: 100%;
  z-index: 99999999;
  display: block;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  margin-bottom: 100px;
  height: 100%;
  min-height: 850px;
}
#progress-bar {
  width: 300px;
  margin-left: 25px;
  margin-right: 50px;
}
.progress {
  height: 25px;
  width: 300px;
  background-color: #e9ecef;
}
.progress-info {
  color: #000;
  text-align: center;
  font-weight: bold;
}
.progress-bar {
  background-color: red;
  color: red;
  fill: red;

}
</style>
